.mainSection {
  max-width: 1440px;
  padding: 60px 140px 406px 140px;
  color: #151515;
}
.wrapper {
  max-width: 1160px;
  margin-right: auto;
  margin-left: auto;
}
.header {
  font-weight: 700;
  font-size: 40px;
  line-height: 56px;
  letter-spacing: 0.02em;
  margin-bottom: 20px;
}
.textDescription {
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  font-family: 'Kanit', sans-serif;
  a,
  a:visited {
    color: #151515;
    text-decoration: underline;
    font-weight: 300;
  }
  a:hover {
    cursor: pointer;
  }
}
@media (max-width: 860px) {
  .mainSection {
    max-width: 860px;
    padding: 30px 0px 60px 0px;
    margin-right: 16px;
    margin-left: 16px;
  }
  .header {
    font-size: 24px;
    line-height: 34px;
  }
  .descriptionWrapper {
    height: 400px;
  }
  .textDescription {
    font-size: 14px;
    line-height: 22px;
  }
}
