.sliderContainer {
  overflow: hidden;
  margin-top: 80px;
  display: none;
}
.cardContainerRestaurant {
  height: 610px;
  box-shadow: 9px 9px 9px #ebebeb;
  border: 1px solid #d7d7d7;
  border-radius: 11px;
  padding: 24px 28px;
  margin-right: auto;
  margin-left: auto;
  max-width: 320px;
  //min-width: 318px;
  display: flex;
  flex-direction: column;
  -webkit-border-radius: 11px;
  -moz-border-radius: 11px;
  -ms-border-radius: 11px;
  -o-border-radius: 11px;
}
.bodyRestaurant {
  max-width: 260px;
  padding-bottom: 24px;
}
.whyMatter {
  color: #ee3524;
  text-align: center;
  font-size: 18px;
  margin-bottom: 15px;
  margin-top: 15px;
}
.cardTitle {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #151515;
  opacity: 60%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}
.cardHeader {
  font-weight: 700;
  font-size: 24px;
  line-height: 33px;
  color: #ee3524;
  text-align: center;
  margin-bottom: 24px;
  margin-top: 24px;
  max-width: 290px;
}
.cardDescriptionRestaurant {
  font-size: 16px;
  line-height: 24px;
  color: #151515;
  text-align: center;
  max-width: 234px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 1060px) {
  .sliderContainer {
    margin-right: auto;
    margin-left: auto;
    display: block;
    max-width: 100%;
  }
}
@media (max-width: 860px) {
  .sliderContainer {
    max-width: 330px;
  }
}
